@import '../../styles//variabbles';
@import url('https://fonts.googleapis.com/css2?family=Pangolin&display=swap');
.cv-intro {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100vw;
  height: 100vh;

  flex-shrink: 0;
  /* xs (<=543px) */
  @media (max-width: $xs-max) {
    padding-left: 0;
    flex-wrap: wrap;
  }

  /* sm (>=576 и <=767) */
  @media (min-width: $sm) and (max-width: $sm-max) {
    padding-left: 1rem;
  }

  /* md (>=768 и <=991) */
  @media (min-width: $md) and (max-width: $md-max) {
    padding-left: 3rem;
  }

  /* lg (>=992 и <=1199) */
  @media (min-width: $lg) and (max-width: $lg-max) {
    padding-left: 3rem;
  }

  /* xl (>=1200) */
  @media (min-width: $xl) {
    padding-left: 10rem;
  }
}

.cv-description {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  /* xs (<=543px) */
  @media (max-width: $xs-max) {
    width: 100%;
    margin: 1rem;
  }
  /* sm (>=576 и <=767) */
  @media (min-width: $sm) and (max-width: $sm-max) {
    width: 75%;
  }
  /* md (>=768 и <=991) */
  @media (min-width: $md) and (max-width: $md-max) {
    width: 90%;
  }

  /* lg (>=992 и <=1199) */
  @media (min-width: $lg) and (max-width: $lg-max) {
    width: 40%;
  }

  /* xl (>=1200) */
  @media (min-width: $xl) {
    width: 40%;
  }
  .cv-title {
    width: 100%;
    color: $primary;
    font-weight: bold;
    mix-blend-mode: normal;
    text-shadow: 10px 10px 4px rgba(31, 31, 31, 0.34);

    /* xs (<=543px) */
    @media (max-width: $xs-max) {
      font-size: 25px;
      text-align: center;
    }

    /* sm (>=576 и <=767) */
    @media (min-width: $sm) and (max-width: $sm-max) {
      font-size: 30px;
    }

    /* md (>=768 и <=991) */
    @media (min-width: $md) and (max-width: $md-max) {
      font-size: 35px;
    }
    /* lg (>=992 и <=1199) */
    @media (min-width: $lg) and (max-width: $lg-max) {
      font-size: 45px;
    }

    /* xl (>=1200) */
    @media (min-width: $xl) {
      font-size: 85px;
    }
  }
  .cv-subtitle {
    width: 100%;
    color: rgba(31, 31, 31, 0.42);
    font-weight: bold;
    position: relative;
    /* xs (<=543px) */
    @media (max-width: $xs-max) {
      text-align: center;
      bottom: 0;
      left: 5px;
      font-size: $font-S;
    }

    /* sm (>=576 и <=767) */
    @media (min-width: $sm) and (max-width: $sm-max) {
      bottom: 0;
      left: 0.1rem;
      font-size: $font-L;
    }

    /* md (>=768 и <=991) */
    @media (min-width: $md) and (max-width: $md-max) {
      bottom: 10px;
      left: 0;
      font-size: $font-L;
    }
    /* lg (>=992 и <=1199) */
    @media (min-width: $lg) and (max-width: $lg-max) {
      bottom: 15px;
      left: 0;
      font-size: $font-XL;
    }

    /* xl (>=1200) */
    @media (min-width: $xl) {
      bottom: 35px;
      left: 0.4rem;
      font-size: $font-heading_3;
    }
  }
}
.cv {
  opacity: 0;
  &.isActive {
    opacity: 1;
    animation: fadeIn 4s;
  }
  &.isFake {
    opacity: 1;
    animation: fadeIn 4s;
    /* xs (<=543px) */
    @media (max-width: $xs-max) {
      height: 46%;
      width: 100%;
    }

    /* sm (>=576 и <=767) */
    @media (min-width: $sm) and (max-width: $sm-max) {
      height: 46%;
      width: 100%;
    }
  }
  .photoContainer {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    height: 100%;
    .photo {
      background-image: url('../../assets/photos.png');
      background-position: left;
      background-repeat: no-repeat;
      object-fit: cover;
      background-size: contain;
      /* xs (<=543px) */
      @media (max-width: $xs-max) {
        height: 30%;
        width: 50%;
        margin: 1rem 1rem 1rem 1rem;
      }

      /* sm (>=576 и <=767) */
      @media (min-width: $sm) and (max-width: $sm-max) {
        height: 30%;
        width: 100%;
        margin: 10rem 1rem 1rem 1rem;
      }

      /* md (>=768 и <=991) */
      @media (min-width: $md) and (max-width: $md-max) {
        height: 30%;
        width: 100%;
        margin: 1rem 1rem 1rem 1rem;
      }
      /* lg (>=992 и <=1199) */
      @media (min-width: $lg) and (max-width: $lg-max) {
        height: 30%;
        width: 50%;
        margin: 5rem 1rem 1rem 1rem;
      }

      /* xl (>=1200) */
      @media (min-width: $xl) {
        min-width: 40%;
        margin: 5rem 1rem 1rem 1rem;
      }
    }
    .name {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      /* xs (<=543px) */
      @media (max-width: $xs-max) {
        margin: 1rem 1rem 1rem 1rem;
        font-size: $font-L;
        width: 30%;
      }

      /* sm (>=576 и <=767) */
      @media (min-width: $sm) and (max-width: $sm-max) {
        margin: 1rem 1rem 1rem 1rem;
        font-size: $font-XL;
        width: 100%;
      }

      /* md (>=768 и <=991) */
      @media (min-width: $md) and (max-width: $md-max) {
        width: 100%;
        margin: 1rem 1rem 1rem 1rem;
        font-size: $font-XL;
      }
      /* lg (>=992 и <=1199) */
      @media (min-width: $lg) and (max-width: $lg-max) {
        width: 100%;
        margin: 2rem 1rem 1rem 1rem;
        font-size: $font-heading_2;
      }

      /* xl (>=1200) */
      @media (min-width: $xl) {
        width: 50%;
        margin: 5rem 1rem 1rem 1rem;
        font-size: $font-heading_1;
      }
    }
    .cv-information {
      width: 80%;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      flex-wrap: wrap;
      .cv-russia {
        width: 100%;
        font-family: 'Pangolin', cursive;
      }
      /* xs (<=543px) */
      @media (max-width: $xs-max) {
        font-size: $font-S;
        // position: relative;
        // bottom: 20px;
        // left: 1rem;
      }

      /* sm (>=576 и <=767) */
      @media (min-width: $sm) and (max-width: $sm-max) {
        font-size: $font-M;
        // position: relative;
        // bottom: 10px;
        // left: 1rem;
      }

      /* md (>=768 и <=991) */
      @media (min-width: $md) and (max-width: $md-max) {
        font-size: $font-M;
        // position: relative;
        // bottom: 95px;
        // left: 1rem;
      }
      /* lg (>=992 и <=1199) */
      @media (min-width: $lg) and (max-width: $lg-max) {
        font-size: $font-M;
        // position: relative;
        // bottom: 45px;
        // left: 1rem;
      }

      /* xl (>=1200) */
      @media (min-width: $xl) {
        font-size: $font-L;
        // position: relative;
        // bottom: 80px;
        // left: 1rem;
      }
    }
    .fakeInformation {
      animation: fadeIn 4s;
      background-image: url('../../assets/who3.png');
      background-position: center;
      background-repeat: no-repeat;
      object-fit: cover;
      background-size: contain;
      width: 100%;
      height: 100%;
    }
  }

  /* xs (<=543px) */
  @media (max-width: $xs-max) {
    height: 100%;
    width: 100%;
    margin: 0 1rem;
  }

  /* sm (>=576 и <=767) */
  @media (min-width: $sm) and (max-width: $sm-max) {
    height: 100%;
    width: 100%;
  }

  /* md (>=768 и <=991) */
  @media (min-width: $md) and (max-width: $md-max) {
    height: 100%;
    width: 80%;
  }
  /* lg (>=992 и <=1199) */
  @media (min-width: $lg) and (max-width: $lg-max) {
    height: 100%;
    width: 70%;
  }

  /* xl (>=1200) */
  @media (min-width: $xl) {
    height: 100%;
    width: 50%;
  }
}
$time: 6s;
.clearfix::after,
.clearfix::before {
  content: ' ';
  display: table;
}

.section-wrapper {
  width: 50%;
  float: left;
  text-align: left;
  color: $secondary;
  font-weight: 300;
  margin-bottom: 2rem;
  @media (max-width: $xs-max) {
    width: 46%;
  }

  /* sm (>=576 и <=767) */
  @media (min-width: $sm) and (max-width: $sm-max) {
    width: 46%;
  }

  /* md (>=768 и <=991) */
  @media (min-width: $md) and (max-width: $md-max) {
    width: 50%;
  }
  &:nth-child(2n) {
    padding-right: 8%;
  }
  h3.section-title {
    color: $text;
    text-align: left;
    text-transform: uppercase;
    font-size: 1.2em;
    margin-bottom: 20px;
    font-weight: 400;
  }
  .skill-percentage {
    margin-bottom: 10px;
    position: relative;
    &::after {
      content: '';
      width: 100%;
      height: 6px;
      background: $background-light;
      opacity: 0.5;
      display: block;
      margin-top: 3px;
    }
    &::before {
      content: '';
      height: 6px;
      background: $secondary;
      position: absolute;
      margin-top: 3px;
      bottom: 0;
    }
    &:nth-child(1)::before {
      width: 70%;
      animation: skill_1 $time ease;
    }
    &:nth-child(2)::before {
      width: 65%;
      animation: skill_2 $time ease;
    }
    &:nth-child(3)::before {
      width: 55%;
      animation: skill_3 $time ease;
    }
    &:nth-child(4)::before {
      width: 70%;
      animation: skill_4 $time ease;
    }
    &:nth-child(5)::before {
      width: 30%;
      animation: skill_5 $time ease;
    }
    &:nth-child(6)::before {
      width: 20%;
      animation: skill_6 $time ease;
    }
    &:nth-child(7)::before {
      width: 70%;
      animation: skill_6 $time ease;
    }
  }
  .contact-item {
    margin-bottom: 1rem;
    position: relative;
    a {
      color: $primary;
      display: flex;
      margin-bottom: 1rem;
      justify-content: flex-start;
      align-items: center;
      &.git {
        &::before {
          content: ' ';
          background: url('../../assets/icons/github.png') no-repeat center;
          background-size: contain;
          width: 32px;
          height: 32px;
          display: inline-block;
          margin-right: 1rem;
        }
      }
      &.linked {
        &::before {
          content: ' ';
          background: url('../../assets/icons/linkedin.png') no-repeat center;
          background-size: contain;
          width: 32px;
          height: 32px;
          display: inline-block;
          margin-right: 1rem;
        }
      }
      &.telegram {
        &::before {
          content: ' ';
          background: url('../../assets/icons/telegram.png') no-repeat center;
          background-size: contain;
          width: 32px;
          height: 32px;
          display: inline-block;
          margin-right: 1rem;
        }
      }
      &.mail {
        &::before {
          content: ' ';
          background: url('../../assets/icons/gmail.png') no-repeat center;
          background-size: contain;
          width: 32px;
          height: 32px;
          display: inline-block;
          margin-right: 1rem;
        }
      }
    }
  }
}
.cv-link {
  text-decoration: underline;
  animation: fadeIn 3000ms infinite;
  color: $text;
  white-space: nowrap;
  visibility: visible;
  font-size: 3.4rem;
  cursor: pointer;
  /* xs (<=543px) */
  @media (max-width: $xs-max) {
    display: flex;
    flex-direction: row;
    font-size: $font-M;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
}

@keyframes skill_1 {
  from {
    width: 0%;
  }
  to {
    width: 70%;
  }
}
@keyframes skill_2 {
  from {
    width: 0%;
  }
  to {
    width: 65%;
  }
}
@keyframes skill_3 {
  from {
    width: 0%;
  }
  to {
    width: 55%;
  }
}
@keyframes skill_4 {
  from {
    width: 0%;
  }
  to {
    width: 70%;
  }
}
@keyframes skill_5 {
  from {
    width: 0%;
  }
  to {
    width: 20%;
  }
}
@keyframes skill_6 {
  from {
    width: 0%;
  }
  to {
    width: 10%;
  }
}
@keyframes skill_7 {
  from {
    width: 0%;
  }
  to {
    width: 70%;
  }
}
